import * as React from 'react';
import { MISSION1, MISSION2, startGameWithMission } from './Missions';
import { HandCardSprite } from '../game-react/sprites/HandCardSprite';
import { initEngineWorker, getCardDefJson } from '../game-logic/engine';
import { makeDisplayConfig } from '../game-react/sprites/BaseCardSprite';

const styles:any = {
    container: {
        width: 200,
        height: 200,
        backgroundColor: '#77EB11',
        display: 'table',
    },
    paragraph: {
        textAlign: 'center',
        width: '100%',
        height: '100%',
        display: 'table-cell',
        verticalAlign: 'middle'
    },
};


function loading() {
    return <div/>;
}

const PAGE = 3;

function cardScreen({cards}) { // {filter, updateFilter}) {
    return <div style={{position: "fixed", overflowY: "scroll", width: "100%", height: "100%"}}>
        {/* <p style={styles.paragraph}>
            Cards in your deck:
        </p> */}
        {cards.slice(PAGE*16, (PAGE+1)*16).map((c, i) => <HandCardSprite 
            displayConfig={makeDisplayConfig({povPlayerId: 0, x: 280 * (i % 4), y: Math.floor(i / 4) * 400, width: 300, height: 400, zone: 'screen'})}
            cardData={c}
        />)}
    </div>;
}

export class CardView extends React.Component {
    props: { finalState };
    state: { menu, cards };
    constructor(props) {
        super(props);
        this.state = {menu: loading, cards: []};
    }
    componentDidMount() {
        this.loadGame();
    }
    async loadGame() {
        await initEngineWorker();
        const cards = await getCardDefJson();
        console.log(cards)
        this.setState({menu: cardScreen, cards});
    }
    render() {
        return this.state.menu(this.state);
    }
}


